	module.exports = {"data":{"markdownRemark":{"html":"<h1>Split Method</h1>\n<p>The <code>String.Split</code> method parses a string. As input, it takes a character indicating the separator, and generates an array of sub strings.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token keyword\">string</span> myText <span class=\"token operator\">=</span> <span class=\"token string\">\"I like pizza\"</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// Split the string by ' '(space) character.</span>\n<span class=\"token keyword\">string</span><span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span> splitResult <span class=\"token operator\">=</span> myText<span class=\"token punctuation\">.</span><span class=\"token function\">Split</span><span class=\"token punctuation\">(</span><span class=\"token string\">' '</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// The array splitResult, now contains three substrings.</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// Now print the array of substrings</span>\n<span class=\"token keyword\">foreach</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">string</span> x <span class=\"token keyword\">in</span> splitResult<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">{</span>\n    <span class=\"token comment\" spellcheck=\"true\">// Write On Console</span>\n    Console<span class=\"token punctuation\">.</span><span class=\"token function\">WriteLine</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2>Output:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>> I\n> like\n> pizza</code></pre>\n      </div>","fields":{"slug":"/csharp/split/"},"frontmatter":{"title":"Split"}}},"pathContext":{"slug":"/csharp/split/"}}

